	module.exports = {"data":{"markdownRemark":{"html":"<p>Out of the box, the terminal shipped with Ubuntu is a bit bland. This section will let you become a power-user <img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/muscle.png?v=2\" title=\":muscle:\" alt=\":muscle:\" class=\"forum-image\">.</p>\n<h2>Tools:</h2>\n<h4>Terminator</h4>\n<p><a href='https://launchpad.net/terminator' target='_blank' rel='nofollow'>Terminator</a> lets you arrange multiple terminals in a grid-like interface.<br>\nTo install Terminator enter <code>sudo apt-get install terminator</code> into the terminal.</p>\n<p>Screenshot of Terminator:</p>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/6/6af4988ebfb1835ff3c19366865eaaaaf224cb19.png\" alt=\"Screenshot of Terminator\"></p>\n<h4>Oh My ZSH!</h4>\n<p>Prerequisites:  </p>\n<ul>\n<li><code>git</code> should be installed</li>\n</ul>\n<p>To install <code>ZSH</code> and <code>Oh My ZSH!</code> use the following commands:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>sudo apt install zsh && chsh -s $(which zsh)</code></pre>\n      </div>\n<blockquote>\n<p>Note: you will need to log out and in again to use ZSH instead of bash as your default shell.</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>sh -c \"$(curl -fsSL https://raw.githubusercontent.com/robbyrussell/oh-my-zsh/master/tools/install.sh)\"</code></pre>\n      </div>\n<p>Once you restart your terminal, <code>Oh My ZSH!</code> should be installed.</p>\n<p>Checkout the <a href='https://github.com/robbyrussell/oh-my-zsh/wiki' target='_blank' rel='nofollow'>Official Documentation</a> to learn how to install plugins and themes.</p>\n<h4>Advanced Tutorials</h4>\n<ul>\n<li><a href='http://cli.learncodethehardway.org/book/' target='_blank' rel='nofollow'>The Command Line Crash Course</a></li>\n<li><a href='https://github.com/jlevy/the-art-of-command-line' target='_blank' rel='nofollow'>The Art of Command Line</a></li>\n<li><a href='https://www.learnenough.com/command-line-tutorial' target='_blank' rel='nofollow'>Learn Enough Command Line to Be Dangerous</a></li>\n</ul>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/point_left.png?v=2\" title=\":point_left:\" alt=\":point_left:\" class=\"forum-image\"> Previous | <img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/book.png?v=2\" title=\":book:\" alt=\":book:\" class=\"forum-image\"> Home <img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/book.png?v=2\" title=\":book:\" alt=\":book:\" class=\"forum-image\"> | Next <img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/point_right.png?v=2\" title=\":point_right:\" alt=\":point_right:\" class=\"forum-image\"><br>\n<a href=\"//forum.freecodecamp.com/t/customizing-ubuntu/18382\"><strong>Customising Ubuntu</strong></a> | <a href=\"//forum.freecodecamp.com/t/setting-up-ubuntu-for-programming/18388\"><strong>Table of Contents</strong></a> | <a href=\"//forum.freecodecamp.com/t/installing-devtools-and-modern-web-browsers/18385\"><strong>Installing DevTools and modern web browsers</strong></a></p>","fields":{"slug":"/miscellaneous/jazzing-up-the-terminal/"},"frontmatter":{"title":"Jazzing Up the Terminal"}}},"pathContext":{"slug":"/miscellaneous/jazzing-up-the-terminal/"}}

